/* HEADER */
header {
    padding: 15px 30px;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 70px;
    background: rgba(0,0,0,0); /* transparente elegante */
    backdrop-filter: blur(8px);
    z-index: 1000;
    display: flex;
    align-items: center;
    justify-content: center;
}

/* NAV */
nav {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    gap: 15px;
}

/* LINKS */
nav a {
    color: #f0f0f0;
    text-decoration: none;
    padding: 8px 16px;
    font-weight: 500;
    font-size: 2rem;
    border-radius: 8px;
    transition: all 0.3s ease;
    letter-spacing: 0.5px;
}

nav a:hover {
    background: rgb(28, 228, 151);
    color: #111;
    box-shadow: 0px 4px 12px rgba(28, 228, 151, 0.5);
    transform: translateY(-2px); /* sutil levantado */
}

